import React, { useEffect } from "react";
import Header from "./Header";
import { Link,useHistory} from "react-router-dom";
import Topstories from "./topstories";
import Homenews from './homenews';
import Footer from "./footer";
import { useSelector } from "react-redux";



function Home() {
  const history = useHistory();
  const userLogin = useSelector((state) => state.userLogin);
  const { userInfo } = userLogin;

  useEffect(() => {
    if (userInfo) {
      history.push("/dashboard");
    }
  }, [history, userInfo]);

    return (
        <div>
        <Header/>   

        <section className="text-gray-600 body-font bg-green-200 mt-2">
  <div className="container mx-auto flex pl-8 py-17 md:flex-row flex-col items-center">
    <div className="lg:flex-grow md:w-1/2 lg:pr-24 md:pr-16 flex flex-col md:items-start md:text-left mb-16 md:mb-0 items-center text-center">
      <h1 className="title-font sm:text-4xl text-3xl mb-4 font-medium text-gray-900">Before they sold out
      </h1>
      <p className="mb-8 leading-relaxed">Copper mug try-hard pitchfork pour-over freegan heirloom neutra air plant cold-pressed tacos poke beard tote bag. Heirloom echo park mlkshk tote bag selvage hot chicken authentic tumeric truffaut hexagon try-hard chambray.</p>
      <div className="flex justify-center">
        <Link to="/about" className="inline-flex text-white bg-green-500 border-0 py-2 px-6 focus:outline-none hover:bg-green-600 rounded text-lg">About Us</Link>
        <Link to="/writeforus" className="ml-4 inline-flex text-gray-700 bg-gray-100 border-0 py-2 px-6 focus:outline-none hover:bg-gray-200 rounded text-lg">Write with Us</Link>
      </div>
    </div>
    <div className="lg:max-w-lg lg:w-full md:w-1/2 w-5/6 border-white">
      <img className="object-cover object-center " alt="hero" src="https://source.unsplash.com/720x600/?news"/>
    </div>
  </div>
</section>

<Topstories/>
<Homenews/>
<Footer/>
        </div>
    )
}
export default Home;